.hero-image {
  width: 100%;
  height: 40rem;
}

.logo-button{
  position: absolute;
  left: 1%;
  top: 1rem;
  width: 15rem;
  height: 5rem;
  background-color: darkolivegreen;
}

.hero-image-text {
  background-color: black;
  position: absolute;
  opacity: 75%;
  left: 4rem;
  top: 25rem;
  width: 91%;
  height: 15rem;
  z-index: 0.98;
}

.hero-image-text h3 {
  position: absolute;
  left: 22rem;
  top: -1rem;
  font-size: 2.5rem;
  color: white;
  z-index: 1rem;
  text-align: center;
}

.hero-image-text p {
  position: absolute;
  left: 26rem;
  top: 8rem;
  color: white;
  font-size: 2rem;
  z-index: 1;
  text-align: center;
}

.invitation-text {
  font-size: 2rem;
  position: absolute;
  left: 25rem;
}

.invitation-text button {
  position: absolute;
  left: 20rem;
  width: 15rem;
  height: 3rem;
  font-size: 1.5rem;
  background-color: white;
  border-radius: 0.5rem;
}

.logo {
  position: absolute;
  left: 22.6rem;
  top: 43rem;
  width: 10rem;
  height: 3rem;
  background-color: red;
}

.footer {
  position: absolute;
  left: 0rem;
  top: 90rem;
  width: 100%;
  height: 20rem;
  background-color: teal;
  color: white;
}

.footer p {
  position: absolute;
  left: 15rem;
  top: 3rem;
}

.landing-page-title {
  position: absolute;
  left: 5rem;
}

.landing-page-banner {
  position: absolute;
  left: 5rem;
  top: 6rem;
}

.banner-video {
  position: absolute;
  width: 25rem;
  height: 15rem;
  top: 10rem;
  background-color: white;
  border: solid black;
  opacity: 25%;
  z-index: 1;
}

.banner-image {
  position: absolute;
  top: 9rem;
  width: 60rem;
  height: 21rem;
  background-color: red;
  z-index: 0.99;
}

.landing-page-banner h2{
  position: absolute;
  top: 38rem;
  left: 0rem;
  text-align: center;
  width: 55rem;
}

.location-selector {
  position: absolute;
  left: 8rem;
  top: 55rem;
  width: 54rem;
  height: 30rem;
  background-color: red;
}

.landing-page-footer {
  position: absolute;
  top: 90rem;
  width: 100%;
  height: 25rem;
  background-color: red;
}

.topnav-banner {
  position: absolute;
  width: 100%;
  height: 10rem;
  display: inline-block;
}

.left-align {
  float: left;
  width: 49%;
}

.left-align h1 {
  position: absolute;
  left: 5%;
  top: 20%;
}

.right-align {
  float: left;
  width: 51%;
}

.right-align button {
  position: absolute;
  right: 5%;
  top: 40%;
  background-color: chocolate;
  color: white;
  border: none;
  padding: 1rem;
  font-size: 1rem;
}

body, html, #root, .app, .container {

  width: 100%;

  height: 100%;

}



.container {

  font-size: 28px;

  color: #fff;

  display: flex;

  flex-direction: column;

  align-items: left;
  padding-left: 5%;

  justify-content: center;

  background-color: #282c34;

}



.loginForm {

 box-sizing: border-box;

 width: 100%;

 max-width: 400px;

 padding-left: 20px;

 padding-right: 20px;

}
.registrationForm {

  box-sizing: border-box;
 
  width: 100%;
 
  max-width: 400px;
 
  padding-left: 20px;
 
  padding-right: 20px;
 
 }


.inputField {

 padding-top: 16px;

}



.input {

 box-sizing: border-box;

 outline: none;

 border: solid 2px #1189de;

 border-radius: 4px;

 color: #292929;

 width: 100%;

 padding: 12px;

 font-size: 14px;

 background: rgba(255, 255, 255, 1);

}



.submitButton {

 padding-top: 16px;

}



.btn {

 width: 100%;

 min-width: 280px;

 color: #565656;

 padding: 12px;

 font-size: 14px;

 font-weight: bold;

 border: solid 2px #1189de;

 border-radius: 4px;

 background: #fff;

 cursor: pointer;

}